<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">{{$route.query.type == 1 ? '公告详情' : '活动详情'}}</p>
        </div>
        <div class="c333 f28" style="padding:1rem 0 1.7rem;">
            <div class="nameBox b_line">
                <p class="subtitle f32">{{data.title}}</p>
                <p class="c999 f22">{{data.createTime}}</p>
                <p class="c666 mt-20" style="line-height:.5rem;">{{data.afficheDesc}}</p>
            </div>
            <div class="content">
                <p class="subtitle">图片</p>
                <div class="picture">
                    <img  :src="$api.fileUrl + item" alt="" v-for="(item,index) in data.imageUrls" :key="index">
                </div>
                <p class="subtitle">视频</p>
                <div class="picture">
                    <video :src="$api.fileUrl + item" alt="" v-for="(item,index) in data.videoUrls" :key="index"></video>
                </div>
            </div>  
        </div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            data:{
                afficheDesc:'',
                createTime:'',
                imageUrls:[],
                videoUrls:[],
                title:''
            }
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        issue(){

        },
        getAfficheClassDesc(){//公告详情/活动详情
            this.$toast.loading({
                mask: true,
                duration:0
            });
            this.$http({url:this.$api.message.getAfficheClassDesc},{id:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.data = data.data;
                    this.$toast.clear();
                }else{
                    this.$toast(data,msg);
                }
            },err => {
                this.$toast('出错了');
            })
        }
    },
    created() {
        this.getAfficheClassDesc();
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    .nameBox{
        padding:0 .3rem 1rem;
        line-height: .6rem;
        justify-content: space-between;
    }
    .name{
        border:0;
        text-align: right;
        width:50%;
    }
    .content{
        padding:0 .3rem .3rem;
    }
    .subtitle{
        line-height: .9rem;
    }
    .content textarea{
        width:100%;
        height:2.67rem;
        border:0;
    }
    .picture img{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
        display: block;
    }
    .picture video{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
        display: block;
    }
</style>




